aside {
  position: fixed;
  top: 0;
  left: 0;
  display: flex;
  flex-direction: column;
  justify-content: space-between;
  height: 100vh;
  background: var(--color-red);
  padding: 1rem;
  padding-bottom: 2rem;
  width: 6rem;
  color: white;
}

.logo {
  padding: 0.5rem;
  width: 100%;
  border-bottom: 1px solid rgba(255, 255, 255, 0.4);
  text-align: center;
}

.nav-list {
  display: flex;
  flex-direction: column;
  align-items: center;
  gap: 1rem;
  padding-top: 1rem;
}

.nav-icon {
  color: white;
  font-size: 1.6rem;
}

.footer {
  display: flex;
  justify-content: center;
  width: 100%;
  border-top: 1px solid rgba(255, 255, 255, 0.4);
  padding-top: 1rem;
}

/*------------------------------Responsive------------------------------*/
@media screen and (max-width: 480px) {
}

@media screen and (max-width: 768px) {
  aside {
    width: 100vw;
    height: 4rem;
    top: auto;
    bottom: 0;
    justify-content: center;
    align-items: center;
  }

  .nav-menu {
    display: flex;

    width: 100%;
  }

  .nav-list {
    width: 100%;
    justify-content: space-evenly;
    flex-direction: row;
  }

  .logo {
    display: none;
  }

  .footer {
    display: none;
  }
}

