.skills-container {
  display: flex;
  flex-direction: column;
  padding-top: 5rem;
  gap: 2rem;
}

.skills-content {
  display: flex;
  flex-wrap: wrap;
  gap: 1rem;
}

.skill-box {
  display: flex;
  align-items: center;
  gap: 0.5rem;
  background: var(--color-blue);
  border-radius: 2rem;
  padding: 1rem 1.5rem;
  color: white;
  font-size: 1rem;
  font-weight: bold;
  cursor: pointer;
}

