.portfolio-container {
  padding: 5rem 0;
}

.projects {
  display: grid;
  grid-template-columns: repeat(3, 1fr);
  margin-top: 2rem;
  gap: 2rem;
}

.project-card {
  height: 25rem;
  background: white;
  overflow: hidden;
  border-radius: 0.2rem;
  box-shadow: var(--box-shadow);
}

.project-img {
  width: 100%;
  height: 15rem;
}

.project-img img {
  width: 100%;
  height: 100%;
  object-fit: cover;
}

.project-content {
  display: flex;
  flex-direction: column;
  height: 10rem;
  justify-content: space-between;
  padding: 1rem;
}

.project-detail {
  text-align: center;
}

.project-buttons {
  width: 100%;
  display: flex;
  justify-content: center;
  gap: 1rem;
}

/*------------------------------Responsive------------------------------*/
@media screen and (max-width: 480px) {
  .project-buttons button {
    border-radius: 1.5rem;
    padding: 0.7rem 1rem;
    font-size: 0.7rem;
  }
}

@media screen and (max-width: 768px) {
}

@media screen and (max-width: 1024px) {
  .projects {
    display: flex;
    flex-direction: column;
  }
}

@media screen and (max-width: 1280px) {
  .projects {
    grid-template-columns: 1fr 1fr;
  }
}

@media screen and (max-width: 1440px) {
}
